            <p class="mt-4">这个网站是我搭建的，你只要点进来它就会在。受限于我的技术和时间，它只能做到现在这样，我想，就让它随着我们的爱情一起越变越好吧。</p> 
            <p class="mt-4">清荷，大胆地开心地自由地去做你想做的事，我愿意变成你的底气，你的退路。</p> 
          </div>
          
          <div class="text-right">
            <p class="text-ocean-dark font-semibold text-xl">永远陪着你的夏</p>
            <p class="text-gray-500 mt-2">💙 爱你的心永不变 💙</p>
            <p class="text-gray-400 mt-4" id="current-date">2025年11月27日</p>
          </div>
        </div>
      </main>

      <!-- 底部互动区域 -->
      <footer class="mt-12 text-center">
        <div class="flex justify-center gap-6 mb-8">
          <button @click="toggleDanmu" class="bg-ocean-blue hover:bg-ocean-dark text-white px-6 py-3 rounded-full shadow-md transition-all duration-300 flex items-center gap-2">
            <i class="fa fa-commenting-o"></i>
            <span>{{ danmuPaused ? '开启弹幕' : '暂停弹幕' }}</span>
          </button>
          <button @click="addRandomDanmu" class="bg-ocean-cyan hover:bg-ocean-teal text-white px-6 py-3 rounded-full shadow-md transition-all duration-300 flex items-center gap-2">
            <i class="fa fa-heart"></i>
            <span>发送弹幕</span>
          </button>
        </div>
        
        <p class="text-gray-500 text-sm">
          <i class="fa fa-heart text-ocean-blue mr-1"></i>
          制作于充满爱的日子里————遇见你的第10天
          <i class="fa fa-heart text-ocean-blue ml-1"></i>
        </p>
      </footer>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, onUnmounted } from 'vue'

// 密码验证相关变量
const password = ref('')
const isAuthenticated = ref(false)
const errorMsg = ref('')
const SECRET_PASSWORD = 'woxihuanxia' // 请替换为你的密码
const AUTH_KEY = 'ocean_love_auth' // 用于localStorage存储的键

// 弹幕相关数据
const danmuList = ref([
  "宝贝，早安呀～",
  "宝宝，你昨天几点睡的",
  "好幸福啊，嘿嘿",
  "老公你好man",
  "看谁的聊天记录？不是你的为什么跟你说，你笨蛋啊",
  "店长说我一天摸手链摸800次",
  "我好想你呀！",
  "清荷今天真飒！",
  "面壁思过",
  "你为什么喜欢我啊？我永远愿意回答你",
  "叫姐姐，或者小妈",
  "刚刚有个叫xyx的同学非说这是他老婆",
  "我很听话哦，我都吃完了！",
  "桂花酒酿 金桔柠檬",
  "地址给我，我给你点",
  "哪天下班早，我来找你吧",
  "我以后可以每天都给你唱一首歌",
  "给夏医生的谢礼哦",
  "笨蛋清荷",
  "你喜欢女孩子撒娇啊？",
  "别是见色起意吧",
  "想抱抱你可以吗",
  "你刚刚摸我！",
  "我摸了！我骄傲！",
  "你老公",
  "你好真诚啊夏同学",
  "走柳踹蒋伯驾！",
  "你摸我！",
  "夏，你那天是不是碰我额头了",
  "清荷今天也很勇敢！",
  "咱们俩~口~子~",
  "有你真好",
  "永远喜欢你",
  "姐姐能教200多个月的小孩子写字吗，手把手哦",
  "那我下次出门前清理好耳朵",
  "我会一直陪着你",
  "木马，我说的是游乐场的木马！",
  "你今天有没有想我？",
  "最喜欢你啦！",
  "我好想你呀",
  "有你在什么都不怕",
  "你是我的小宝贝",
  "欸……天地良心…非～礼～啊！！！！",
  "天蝎座……撩的我想死",
  "是男女朋友吗就约会",
  "我的世界欢迎你 我要奔向你的世界",
  "你就说这样爽不爽吧"
])

// 弹幕颜色调整为半透明海洋色系
const danmuColors = ref([
  'bg-ocean-blue/70 text-gray-900',      // 海洋蓝背景 + 深灰文字
  'bg-ocean-cyan/70 text-gray-900',      // 海洋青背景 + 深灰文字
  'bg-ocean-teal/70 text-gray-900',      // 海洋绿背景 + 深灰文字
  'bg-ocean-dark/70 text-yellow-800',    // 深海色背景 + 浅黄文字
  'bg-blue-100/80 text-ocean-dark',      // 浅蓝背景 + 深海文字
  'bg-cyan-100/80 text-ocean-dark',      // 浅青背景 + 深海文字
  'bg-teal-100/80 text-ocean-dark',      // 浅绿背景 + 深海文字
  'bg-white/80 text-ocean-blue border border-ocean-blue/30',
  'bg-pink-100/80 text-pink-800',        // 新增：浅粉背景 + 深粉文字
  'bg-purple-100/80 text-purple-800',    // 新增：浅紫背景 + 深紫文字
  'bg-yellow-100/80 text-yellow-800',    // 新增：浅黄背景 + 深黄文字
  'bg-orange-100/80 text-orange-800'     // 新增：浅橙背景 + 深橙文字
])

const danmuPaused = ref(false)
let danmuInterval = null

// 生命周期：组件挂载时执行
onMounted(() => {
  // 检查本地存储中是否有验证记录
  const savedAuth = localStorage.getItem(AUTH_KEY)
  if (savedAuth === btoa(SECRET_PASSWORD)) {
    isAuthenticated.value = true
  }
  
  // 设置当前日期
  const date = new Date()
  const year = date.getFullYear()
  const month = String(date.getMonth() + 1).padStart(2, '0')
  const day = String(date.getDate()).padStart(2, '0')
  document.getElementById('current-date').textContent = `${year}年${month}月${day}日`
  
  // 初始化弹幕（仅在验证通过后）
  if (isAuthenticated.value) {
    initDanmu()
    
    // 定时发送弹幕（3秒/条）
    danmuInterval = setInterval(() => {
      if (!danmuPaused.value) {
        addRandomDanmu()
      }
    }, 3000)
  }
})

// 生命周期：组件卸载时执行
onUnmounted(() => {
  // 清除定时器，避免内存泄漏
  if (danmuInterval) {
    clearInterval(danmuInterval)
  }
})

// 验证密码
const verifyPassword = () => {
  if (password.value === SECRET_PASSWORD) {
    isAuthenticated.value = true
    errorMsg.value = ''
    // 存储验证状态
    localStorage.setItem(AUTH_KEY, btoa(SECRET_PASSWORD))
    // 验证通过后初始化弹幕
    initDanmu()
    danmuInterval = setInterval(() => {
      if (!danmuPaused.value) {
        addRandomDanmu()
      }
    }, 3000)
  } else {
    errorMsg.value = '密码错误，请重新输入'
    password.value = ''
  }
}

// 初始化弹幕（首次加载5条）
const initDanmu = () => {
  for (let i = 0; i < 5; i++) {
    setTimeout(() => {
      addRandomDanmu()
    }, i * 800)
  }
}

// 添加随机弹幕（圆形气泡效果）
const addRandomDanmu = () => {
  const danmuContainer = document.getElementById('danmu-container')
  if (!danmuContainer) return
  
  // 随机选择弹幕内容、颜色、位置、速度
  const text = danmuList.value[Math.floor(Math.random() * danmuList.value.length)]
  const colorClass = danmuColors.value[Math.floor(Math.random() * danmuColors.value.length)]
  const top = Math.floor(Math.random() * 60) + 20 // 限制在信件主要区域内（20%-80%）
  const left = Math.floor(Math.random() * 40) + 80// 调整为30%-70%
  const duration = Math.floor(Math.random() * 10) + 15 // 漂浮速度：15-25秒
  const size = Math.random() > 0.5 ? 'text-sm' : 'text-xs'
  const scale = 0.6 + Math.random() * 0.5; // 随机大小（0.7-1.2倍）
  
  // 创建弹幕元素
  const danmu = document.createElement('div')
  danmu.className = `danmu ${colorClass} ${size} shadow-md`
  danmu.textContent = text
  danmu.style.top = `${top}%`
  danmu.style.left = `${left}%`
  danmu.style.animationDuration = `${duration}s`
  danmu.style.transform = `scale(${scale})`
  
  // 添加到容器
  danmuContainer.appendChild(danmu)
  
  // 弹幕结束后移除元素（避免DOM堆积）
  setTimeout(() => {
    danmu.remove()
  }, duration * 1000)
}

// 暂停/开启弹幕
const toggleDanmu = () => {
  danmuPaused.value = !danmuPaused.value
  const danmus = document.querySelectorAll('.danmu')
  
  danmus.forEach(danmu => {
    danmu.style.animationPlayState = danmuPaused.value ? 'paused' : 'running'
  })
}
</script>

