  transform-origin: center;
  box-sizing: border-box; /* 确保内边距不增加元素总宽度 */
  max-width: 70%; /* 限制最大宽度，避免超出屏幕 */
}

/* 爱心跳动动画 */
@keyframes heartBeat {
  0% { transform: scale(1); }
